@charset "UTF-8";
:root {
  --base-font: "Zen Kaku Gothic New", sans-serif;
  --second-font: "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

:root {
  --light: 300;
  --normal: 400;
  --medium: 500;
  --semi-bold: 600;
  --bold: 700;
}

:root {
  --black: #333;
  --white: #fff;
  --main: #00561F;
  --light-green: #A5D4AD;
  --light-beige: #FAF2EE;
  --light-gray: #E8E8E8;
  --gray: #707070;
  --gray2: #D7D7D7;
  --dark-blue: #003567;
  --link: #4182B4;
  --bg-light-gray: #F5F5F5;
  --bg-light-green: #EBF6EF;
  --bg-light-blue: #E1EAFA;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.6vw;
  }
}
@media (min-width: 1000px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

/* hover指定できるPCを想定したスタイル */
@media (hover: hover) {
  a:hover {
    opacity: 0.8;
  }
}
/* hoverが使えないタッチ端末を想定した装飾 */
@media (hover: none) {
  a:active {
    opacity: 0.8;
  }
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100svh;
  text-rendering: optimizeSpeed;
  font-size: 1.125rem;
  font-weight: var(--medium);
  font-family: var(--base-font);
  color: var(--black);
  line-height: 1.8;
  word-wrap: break-word;
  overflow-wrap: break-word;
}
@media screen and (min-width: 768px) {
  body {
    font-size: 1rem;
    line-height: 2;
  }
}

main {
  flex: 1;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
/* Set core body defaults */
body {
  min-height: 100svh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.c-btn {
  padding: 0.625rem 2.5rem;
  background-color: black;
  color: yellow;
}

.c-test {
  color: red;
  font-size: 1.25rem;
}
@media screen and (min-width: 768px) {
  .c-test {
    font-size: 3.125rem;
    color: green;
  }
}

.c-title {
  font-size: 10px;
}
@media screen and (min-width: 768px) {
  .c-title {
    font-size: 20px;
  }
}

.l-mt50 {
  margin-top: 3.125rem;
}

.p-common-block {
  background-color: var(--bg-light-gray);
  padding-block: 3.125rem;
}

.p-common-block__inner {
  display: grid;
  gap: 1.875rem;
}
@media screen and (min-width: 768px) {
  .p-common-block__inner {
    grid-template-columns: 40% 1fr;
    gap: 3.125rem;
  }
}

.p-common-block__img img {
  aspect-ratio: 400/248;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.p-common-block__text {
  font-size: 1rem;
  font-weight: var(--medium);
  line-height: 1.8125;
}
@media screen and (min-width: 768px) {
  .p-common-block__text {
    font-size: 1.125rem;
    line-height: 1.5;
  }
}

.c-link-text {
  color: var(--link);
  transition: color 0.3s ease;
}

@media (any-hover: hover) {
  .c-link-text:hover {
    opacity: 1;
    color: var(--main);
  }
}
h1 {
  font-size: 1.25rem;
  font-weight: var(--bold);
  line-height: 1.4;
  letter-spacing: 0.05em;
  padding-bottom: 1.0625rem;
  position: relative;
  display: inline-grid;
  width: fit-content;
  place-items: center;
}
@media screen and (min-width: 768px) {
  h1 {
    font-size: 2.5rem;
    letter-spacing: initial;
  }
}

h1::after {
  display: block;
  position: absolute;
  bottom: 0;
  content: "";
  background-color: var(--main);
  width: 2.5rem;
  height: 0.25rem;
  border-radius: 100vh;
}

h2 {
  font-size: 1.125rem;
  font-weight: var(--bold);
  line-height: 1.5;
  letter-spacing: 0.05em;
  padding-bottom: 0.625rem;
  border-bottom: 0.1875rem solid var(--light-green);
}
@media screen and (min-width: 768px) {
  h2 {
    font-size: 2rem;
    letter-spacing: initial;
    border-width: 0.3125rem;
  }
}

h3 {
  font-size: 1rem;
  font-weight: var(--bold);
  line-height: 1.5;
  padding-bottom: 0.625rem;
  border-bottom: 0.1875rem solid var(--main);
}
@media screen and (min-width: 768px) {
  h3 {
    font-size: 1.5rem;
  }
}

h4 {
  font-size: 1rem;
  font-weight: var(--bold);
  line-height: 1.5;
  padding-block: 0.8125rem;
  padding-left: 2.5rem;
  position: relative;
}
@media screen and (min-width: 768px) {
  h4 {
    font-size: 1.5rem;
    padding-block: 1rem;
    padding-left: 3.125rem;
  }
}

h4::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--light-green);
  width: 1.25rem;
  height: 100%;
}
@media screen and (min-width: 768px) {
  h4::before {
    width: 1.5rem;
  }
}

.c-button-blank, .c-button {
  display: inline-grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 1rem;
  align-items: center;
  text-align: center;
  padding: 0.625rem 1.5rem;
  font-size: 1rem;
  font-weight: var(--bold);
  color: var(--main);
  line-height: 1.8125;
  text-align: center;
  border-radius: 3.5625rem;
  border: 0.125rem solid;
  min-width: 18.75rem;
  transition: background-color 0.3s ease;
}

.c-button-blank::before, .c-button::before {
  content: "";
}

.c-button-blank::after, .c-button::after {
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 1.125rem;
  aspect-ratio: 18/12;
  justify-self: flex-end;
}

.c-button::after {
  background-image: url(../images/common/arrow.svg);
}

.c-button-blank {
  font-size: 0.875rem;
  line-height: 1.5;
  grid-template-columns: 1fr auto;
}

.c-button-blank::before {
  content: none;
}

.c-button-blank::after {
  background-image: url(../images/common/icon-blank.svg);
  width: 1.125rem;
  aspect-ratio: 1/1;
}

.c-link-arrow {
  display: inline-grid;
  grid-template-columns: auto 1fr;
  gap: 0.5rem;
  align-items: center;
  font-size: 0.875rem;
  line-height: 1.2857142857;
}
@media screen and (min-width: 768px) {
  .c-link-arrow {
    gap: 1.125rem;
    font-size: 1rem;
    line-height: 1.5;
  }
}

.c-link-arrow::before {
  content: "";
  background-image: url(../images/common/arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 0.625rem;
  aspect-ratio: 10/9;
}
@media screen and (min-width: 768px) {
  .c-link-arrow::before {
    width: 1.0625rem;
  }
}

.c-frame {
  background-color: var(--bg-light-gray);
  padding-top: 1.875rem;
  padding-right: 1.25rem;
  padding-bottom: 1.4375rem;
  padding-left: 2.5rem;
  border-bottom: 0.625rem solid var(--gray2);
  position: relative;
  font-size: 1.125rem;
  font-weight: var(--bold);
  color: var(--dark-blue);
  line-height: 1.7777777778;
}
@media screen and (min-width: 768px) {
  .c-frame {
    padding-top: 2.875rem;
    padding-right: 1.375rem;
    padding-bottom: 2.375rem;
    padding-left: 4.5625rem;
    font-size: 1.5rem;
    line-height: 2;
  }
}

.c-frame::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--link);
  width: 1.25rem;
  height: calc(100% + 0.625rem);
}
@media screen and (min-width: 768px) {
  .c-frame::before {
    width: 1.875rem;
  }
}

blockquote {
  background-color: var(--bg-light-gray);
  border-radius: 0.625rem;
  padding: 1.875rem 1.25rem;
  font-size: 1rem;
  line-height: 2;
}

.l-footer {
  position: sticky;
  top: 100vh;
}

.l-inner {
  margin: 0 auto;
  max-width: 33.75rem;
  width: 100%;
  height: inherit;
  padding-right: 20px;
  padding-left: 20px;
}
@media screen and (min-width: 768px) {
  .l-inner {
    max-width: 1050px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.p-header {
  height: 4rem;
  background-color: #fff;
}
@media screen and (min-width: 768px) {
  .p-header {
    height: 5rem;
  }
}

.p-header__inner {
  padding-left: 0.9375rem;
  height: inherit;
  display: flex;
  justify-content: space-between;
}
@media screen and (min-width: 768px) {
  .p-header__inner {
    padding-left: 2.1875rem;
  }
}

.p-header__logo {
  max-width: 8.75rem;
  width: 100%;
  height: inherit;
}
@media screen and (min-width: 768px) {
  .p-header__logo {
    max-width: 15rem;
  }
}

.p-header__logo a {
  height: inherit;
  display: flex;
  align-items: center;
}

.p-header__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.p-header__nav {
  position: relative;
  display: none;
  height: inherit;
}
@media screen and (min-width: 768px) {
  .p-header__nav {
    display: block;
  }
}

.p-header__nav-list {
  display: flex;
  height: inherit;
}

.p-header__nav-item,
.p-header__nav-modal-wrap {
  height: inherit;
}

.p-header__nav-item.p-header__nav-item--contact {
  margin-left: 0.9375rem;
  display: flex;
  align-items: center;
}

.p-header__nav-item > a,
.p-header__nav-modal-wrap > p {
  padding: 0 0.9375rem;
  height: inherit;
  display: flex;
  align-items: center;
  font-size: 0.9375rem;
  font-weight: 500;
  line-height: 1;
  color: #333;
}

.p-header__nav-modal-wrap > p {
  transition: 0.3s;
}

.p-header__nav-modal {
  padding: 0.625rem 0;
  position: absolute;
  top: 5rem;
  left: -0.9375rem;
  transform: rotateX(90deg);
  max-width: 25rem;
  width: 100%;
  background-color: #E0E5E5;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}

.p-header__nav-modal-wrap:hover > .p-header__nav-modal {
  transform: rotateX(0deg);
  opacity: 1;
  visibility: visible;
}

.p-header__nav-modal-item a {
  padding: 0.9375rem 0 0.9375rem 3.125rem;
  position: relative;
  display: block;
  font-size: 0.9375rem;
  font-weight: 500;
  line-height: 1;
  color: #333;
}

.p-header__nav-modal-item a::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 2.5rem;
  width: 0.5rem;
  height: 0.5rem;
  border-top: 0.125rem solid #333;
  border-right: 0.125rem solid #333;
  transform: translateY(-50%) rotate(45deg);
  transition: 0.3s;
}

@media screen and (min-width: 768px) {
  .p-header__nav-modal-item a:hover::before {
    opacity: 0.8;
  }
}

.p-header__nav-item.p-header__nav-item--contact a {
  padding: 0 3.125rem;
  position: relative;
  position: relative;
  color: #fff;
  text-align: center;
  background-color: #234F5E;
}

.p-header__nav-item.p-header__nav-item--contact a::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0.9375rem;
  transform: translateY(-50%);
  width: 1.25rem;
  height: 1.25rem;
  display: inline-block;
  background-image: url(./../images/common/mail-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.p-header__nav-item.p-header__nav-item--contact a::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.25rem;
  width: 0.3125rem;
  height: 0.3125rem;
  border-top: 0.125rem solid #fff;
  border-right: 0.125rem solid #fff;
  transform: translateY(-50%) rotate(45deg);
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .p-header__nav-item.p-header__nav-item--contact a::after {
    width: 0.625rem;
    height: 0.625rem;
  }
}

.p-header__hamburger {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  position: relative;
  z-index: 999;
  width: 4rem;
  height: inherit;
  background-color: #234F5E;
  cursor: pointer;
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .p-header__hamburger {
    display: none;
  }
}

.p-header__hamburger span {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  width: 1.5625rem;
  height: 1px;
  background-color: #fff;
  transition: 0.5s;
}

.p-header__hamburger span:nth-of-type(1) {
  top: -0.5rem;
}

.p-header__hamburger span:nth-of-type(2) {
  top: 0;
}

.p-header__hamburger span:nth-of-type(3) {
  top: 0.5rem;
}

.p-header__hamburger.is-open span:nth-of-type(1) {
  top: 0;
  transform: translateX(-50%) rotate(45deg);
}

.p-header__hamburger.is-open span:nth-of-type(2) {
  opacity: 0;
}

.p-header__hamburger.is-open span:nth-of-type(3) {
  top: -0.125rem;
  transform: translateX(-50%) rotate(-45deg);
}

.p-header__drawer {
  margin-top: 4rem;
  padding-bottom: 6.875rem;
  display: none;
  position: absolute;
  z-index: 900;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #EFF4F5;
  overflow-y: scroll;
  scrollbar-width: none;
}

.p-header__drawer::-webkit-scrollbar {
  display: none;
}

.p-header__drawer-item {
  border-bottom: 1px solid #fff;
}

.p-header__drawer-item.p-header__drawer-item--contact {
  margin-top: 2.5rem;
  margin-left: 2rem;
  border-bottom: none;
}

.p-header__drawer-item > a,
.p-header__drawer-accordion-title {
  padding: 1.25rem 2rem;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.05em;
  cursor: pointer;
}

.p-header__drawer-item.p-header__drawer-item--contact a {
  padding: 0.9375rem 0;
  position: relative;
  max-width: 11.25rem;
  width: 100%;
  color: #fff;
  text-align: center;
  background-color: #234F5E;
}

.p-header__drawer-item.p-header__drawer-item--contact a::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 1.25rem;
  transform: translateY(-50%);
  width: 0.9375rem;
  height: 0.9375rem;
  display: inline-block;
  background-image: url(./../images/common/mail-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.p-header__drawer-item.p-header__drawer-item--contact a::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.25rem;
  width: 0.5rem;
  height: 0.5rem;
  border-top: 0.125rem solid #fff;
  border-right: 0.125rem solid #fff;
  transform: translateY(-50%) rotate(45deg);
  transition: 0.3s;
}

.p-header__drawer-accordion-title {
  position: relative;
}

.p-header__drawer-accordion-title::before,
.p-header__drawer-accordion-title::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.25rem;
  width: 0.75rem;
  height: 0.125rem;
  display: block;
  background: #333;
  transition: 0.3s;
}

.p-header__drawer-accordion-title::before {
  transform: translateY(-50%);
}

.p-header__drawer-accordion-title::after {
  transform: translateY(-50%) rotate(90deg);
}

.p-header__drawer-accordion-title.is-open::after {
  transform: translateY(-50%);
}

.p-header__drawer-accordion-list {
  padding-bottom: 0.625rem;
  display: none;
}

.p-header__drawer-accordion-list a {
  padding: 0.625rem 2rem;
  display: block;
  font-size: 0.75rem;
  line-height: 1;
  letter-spacing: 0.05em;
}

.p-test {
  color: red;
  font-size: 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-test {
    font-size: 3.125rem;
    color: green;
  }
}